/* Notices */
.wpwheels-notice {
  border: none;
}
.wpwheels-notice-wrapper {
  padding: 24px;
}
.wpwheels-notice-intro {
  margin-bottom: 32px;
}
.wpwheels-notice-intro img.emoji {
  height: 3em !important;
  width: 3em !important;
}
.wpwheels-notice-intro .intro__hello,
.wpwheels-dashboard-wrapper .intro__hello {
  font-size: 16px;
  font-weight: 600;
  color: #1e1e1e;

  display: flex;
  align-items: center;
  gap: 8px;

  margin-bottom: 16px;
}
.wpwheels-notice-intro .intro__desc,
.wpwheels-dashboard-wrapper .intro__desc {
  margin-top: 12px;
  color: #1e1e1e;
}
.wpwheels-notice-intro .intro__thanks sup,
.wpwheels-dashboard-wrapper .intro__thanks sup {
  color: #fff;
  background-color: #16a7f2;
  font-size: 12px;
  padding: 4px 8px;
  border-radius: 50px;
  letter-spacing: 0.5px;
}
.wpwheels-notice-message {
  display: flex;
  justify-content: left;
  align-items: center;
  gap: 24px;
}
@media (max-width: 768px) {
  #wpwheels-welcome-notice .wpwheels-notice-message {
    flex-wrap: wrap;
  }
}
.wpwheels-notice-image {
  max-width: 200px;
  display: none;
}
.wpwheels-notice-image img {
  width: 100%;
}
@media (min-width: 992px) {
  .wpwheels-notice-image {
    display: block;
  }
}
.wpwheels-notice .button,
.wpwheels-dashboard-wrapper .button {
  padding: 12px 18px;
  line-height: 1em;
}
.wpwheels-notice .button-primary,
.wpwheels-dashboard-wrapper .button-primary {
  color: #fff;
  border-color: #3858e9;
  background-color: #3858e9;
}
.wpwheels-notice h2,
.wpwheels-notice h3 {
  margin: 0 0 10px;
}
.wpwheels-notice-review-content .wpwheels-notice-intro {
  margin-bottom: 0;
}

.wpwheels-review-btn-grp a {
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  line-height: 36px;
  padding: 0 12px;
  color: #2271b1;
  border: 1px solid #2271b1;
  background: #f6f7f7;
  border-radius: 2px;
  margin-right: 8px;
  margin-top: 16px;
}

.wpwheels-review-btn-grp a span {
  font-size: 20px;
  margin-right: 8px;
}

/* Dashboard */
.appearance_page_wpwheels-dashboard #wpwrap {
  background-color: #f8fafc;
}
.appearance_page_wpwheels-dashboard #wpcontent {
  padding-left: 0;
}
.appearance_page_wpwheels-dashboard #wpbody-content .notice {
  display: none !important;
}
.wpwheels-dashboard-wrapper a {
  text-decoration: none;
}
.wpwheels-dashboard-wrapper img {
  max-width: 100%;
  height: auto;
}
/* Header Nav */
.wpwheels-dashboard-header .header-nav-wrapper {
  background-color: #fff;
  border-bottom: 1px solid #e2e8f0;
  padding: 0 1.5rem;
  min-height: 4rem;
  display: flex;
  align-items: stretch;
}
.wpwheels-dashboard-header .header-tabs-wrapper {
  display: flex;
  gap: 32px;
}

.wpwheels-dashboard-header .header-tabs-wrapper a {
  display: inline-flex;
  align-items: center;
  color: #046bd2;
  font-size: 14px;
  font-weight: 500;
  border-bottom: 2px solid #046bd2;
  padding: 8px 0;
}
@media (max-width: 640px) {
  .wpwheels-dashboard-header .header-tabs-wrapper {
    flex-wrap: wrap;
    gap: 12px;
  }
}
.wpwheels-dashboard-header .header-tabs-wrapper a:not(.active) {
  border-color: transparent;
  color: #475569;
}

.wpwheels-dashboard-header .header-tabs-wrapper a:not(.active):hover {
  color: #1e293b;
  border-color: #cbd5e1;
}

/* Getting started */
.wpwheels-dashboard-body {
  padding: 2rem;
}
.wpwheels-dashboard-body .welcome-section-wrapper {
  background-color: #fff;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
}
.wpwheels-dashboard-wrapper .intro__desc {
  font-size: 16px;
  color: #475569;
  line-height: 1.625rem;
}
.wpwheels-dashboard-wrapper .intro__links {
  margin-top: 24px;
}
.wpwheels-dashboard-wrapper .intro__links a {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  margin: 6px 0 0 0;
}
.wpwheels-dashboard-body .welcome-section-image {
  max-width: 350px;
  display: none;
}
@media (min-width: 992px) {
  .wpwheels-dashboard-body .welcome-section-intro {
    flex-basis: 70%;
  }
  .wpwheels-dashboard-body .welcome-section-image {
    flex-basis: 30%;
    display: block;
  }
}

.wpwheels-dashboard-body .welcome-section-image img {
  width: 100%;
  box-shadow: 0 0 10px rgb(0 0 0 / 10%);
}
.wpwheels-dashboard-body .welcome-body-wrapper,
.wpwheels-dashboard-wrapper .wpwheels-upgrade-to-pro .content-wrapper {
  display: grid;
  gap: 32px;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  align-items: flex-start;
  margin-top: 32px;
}
.wpwheels-dashboard-body .welcome-body-wrapper .first-col,
.wpwheels-dashboard-body .welcome-body-wrapper .last-col,
.wpwheels-dashboard-wrapper .wpwheels-upgrade-to-pro .first-col,
.wpwheels-dashboard-wrapper .wpwheels-upgrade-to-pro .last-col {
  display: grid;
  gap: 32px;
  grid-template-columns: repeat(1, minmax(0, 1fr));
}
@media (min-width: 1024px) {
  .wpwheels-dashboard-body .welcome-body-wrapper,
  .wpwheels-dashboard-wrapper .wpwheels-upgrade-to-pro .content-wrapper {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
  .wpwheels-dashboard-body .welcome-body-wrapper .first-col,
  .wpwheels-dashboard-wrapper .wpwheels-upgrade-to-pro .first-col {
    grid-column: span 2 / span 2;
  }
}
.wpwheels-dashboard-wrapper a.link-with-icon {
  font-size: 14px;
  font-weight: 500;
}
.wpwheels-dashboard-wrapper a.link-with-icon span {
  margin-left: 6px;
}
.wpwheels-dashboard-body .customizer-links-wrapper {
  background-color: #fff;
  padding: 2rem;
}
.wpwheels-dashboard-body .customizer-links-heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
}
.wpwheels-dashboard-body .customizer-links-heading p {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  color: #1e293b;
}
.wpwheels-dashboard-body .customizer-links-content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  margin-top: 32px;
}
.wpwheels-dashboard-body .customizer-links-content ul {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin: 0;
}
.wpwheels-dashboard-body .customizer-links-content ul li a {
  font-size: 14px;
  font-weight: 500;
}
.wpwheels-dashboard-body .customizer-links-content .dashicons {
  margin-right: 4px;
}
.wpwheels-dashboard-body .last-col > div {
  background-color: #fff;
  padding: 1.5rem;
}
.wpwheels-dashboard-body .last-col > div h3 {
  margin-top: 0;
}

.wpwheels-dashboard-body .support-section-wrapper {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 24px;
}
@media (min-width: 640px) {
  .wpwheels-dashboard-body .support-section-wrapper {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
.wpwheels-dashboard-body .support-section-wrapper > div {
  background-color: #fff;
  padding: 1.5rem;
}
.wpwheels-dashboard-body .dashicons-star-filled {
  color: #ffac33;
}

/* Upgrade to Pro */
@media (min-width: 768px) {
  .wpwheels-dashboard-wrapper .wpwheels-upgrade-to-pro .content-wrapper {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
  .wpwheels-dashboard-wrapper .wpwheels-upgrade-to-pro .first-col {
    grid-column: span 2 / span 2;
  }
}
.wpwheels-upgrade-to-pro {
  background-color: #000;
  color: #fff;
  padding: 24px 48px;
}
.wpwheels-upgrade-to-pro .feature-title {
  color: #fff;
  margin: 0;
}
.wpwheels-upgrade-to-pro .features-list ul {
  display: flex;
  flex-wrap: wrap;
}
.wpwheels-upgrade-to-pro .features-list ul li {
  display: block;
  width: 100%;
}
@media (min-width: 480px) {
  .wpwheels-upgrade-to-pro .features-list ul li {
    width: 50%;
  }
}
.wpwheels-upgrade-to-pro .dashicons-yes,
.wpwheels-upgrade-to-pro .dashicons-plus {
  color: green;
  margin-right: 6px;
}
.wpwheels-upgrade-to-pro .upgrade-card {
  color: #fff;
  text-align: center;
  padding: 32px;
  box-shadow: 0 0 10px #ff0000;
}
.wpwheels-upgrade-to-pro .upgrade-card p {
  margin: 0 0 24px;
}

.wpwheels-upgrade-to-pro .upgrade-card span {
  background: #0000ff;
  color: #fff;
  padding: 4px 8px;
  margin-bottom: 16px;
  display: inline-block;
  border-radius: 5px;
}

/* Recommended Plugins */
.wpwheels-recommended-plugins {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 24px;
}
@media (min-width: 640px) {
  .wpwheels-recommended-plugins {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
@media (min-width: 992px) {
  .wpwheels-recommended-plugins {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}
.wpwheels-recommended-plugins .wpwheels-plugin-card {
  border: 1px solid #e1e1e1;
}
.wpwheels-recommended-plugins .plugin-wrap {
  padding: 24px;
  background-color: #fff;
}
.wpwheels-recommended-plugins .plugin-action {
  padding: 16px 24px;
}
.wpwheels-recommended-plugins .plugin-title {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 8px;
  margin-bottom: 16px;
  font-size: 12px;
}
.wpwheels-recommended-plugins .plugin-title img {
  max-width: 50px;
}
.wpwheels-recommended-plugins .plugin-action a,
.wpwheels-dashboard-wrapper .starter-template-action a {
  display: inline-flex;
  align-items: center;
  padding: 4px 16px;
  line-height: 1;
  border: none !important;
}
.wpwheels-dashboard-wrapper .starter-template-action a:not(.installing) {
  padding: 12px 18px;
}
.wpwheels-recommended-plugins .plugin-action a.installing:before,
.wpwheels-dashboard-wrapper .starter-template-action a.installing:before {
  color: #fff !important;
}
.wpwheels-recommended-plugins .plugin-action a.activate {
  background-color: #fff !important;
}
.wpwheels-recommended-plugins .plugin-action a.activated {
  color: #008000 !important;
  background-color: #fff !important;
}
.wpwheels-recommended-plugins .plugin-action a.activated:focus {
  box-shadow: none;
}

/* Starter Templates */
.wpwheels-dashboard-body .starter-templates-notice {
  max-width: 40rem;
  height: max-content;
  background-color: #fff;
  margin: auto;
  padding: 64px 32px;
  text-align: center;
}
.wpwheels-dashboard-body .starter-templates-notice h2 {
  font-size: 24px;
  font-weight: 600;
  margin: 0 0 32px;
}
.wpwheels-dashboard-body .starter-template-desc {
  margin: 0 0 32px;
}

/* Free vs Pro*/
